<template>
  <div id="app">
    <top-nav></top-nav>
    <hr>
    <about-container></about-container>
    <hr style="color: #0072CE;height: 2px;">
    <skills></skills>
    <hr style="color: #DA291C;height: 2px;">
    <slider></slider>
    <hr style="color: #43B02A;height: 2px;">
    <contacts-container></contacts-container>
    <hr>
    <footer-container class="pb-3"></footer-container>
  </div>
</template>


<script>
import TopNav from '@/components/top-nav'
import Slider from '@/components/slider'
import Skills from '@/components/skills-container'
import FooterContainer from '@/components/footer-container'
import ContactsContainer from '@/components/contacts-container'
import AboutContainer from '@/components/about-container'

export default {
  components: {
    'top-nav': TopNav,
    'slider': Slider,
    'skills': Skills,
    'footer-container': FooterContainer,
    'about-container': AboutContainer,
    'contacts-container': ContactsContainer,
  },
  computed: {
    title() {
      return this.$t('site.title');
    }
  },
  watch: {
    title(val) {
      document.querySelector('title').innerText = val;
    }
  }
}
</script>