<template>
    <div class="container" id="about-container">
        <div class="row">
            <div class="col-12">
                <h3 class="text-uppercase h5 text-muted" style="font-family: monospace">{{ $t("containers-title.about") }}:</h3>
                <div class="row">
                    <div class="col-md-6 col-lg-4 col-sm-12 text-sm-center">
                        <div class="text-center avatar-img-container">
                            <img src="@/assets/img/jsultanov.jpg" alt="" class="img-fluid">
                        </div>
                    </div>
                    <div class="col-md-6 col-lg-8 col-sm-12 text-sm-center text-md-start">
                        <component v-bind:is="current_lang_about_component"></component>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import ContentRu from './about/content-ru'
import ContentEn from './about/content-en'
import ContentUz from './about/content-uz'

export default {
    components : {
        about_content_ru : ContentRu,
        about_content_en : ContentEn,
        about_content_uz : ContentUz
    },
    computed : {
        current_lang_about_component()
        {
          let defaultComponent = 'about_content_ru';

          if ( this.$i18n.locale !== 'ru')
              defaultComponent = 'about_content_' + this.$i18n.locale;

          return defaultComponent;
        }
    }
}
</script>

<style lang="scss">
#about-container{
    .avatar-img-container{
        a{
            color: #d62828;
        }
        img{
            border-radius: 12px;
            padding: 5px;
            max-width: 300px;
        }
        .info-list{
            a{
                text-decoration: none;
            }
        }
    }
}
</style>