<template>
    <div id="top-nav" class="container pt-5 pb-2">
        <div class="row">
            <div class="col-md-4 col-sm-12 col-xs-12">
                <h1 class="text-uppercase text-center font-fira logoTitle">
                    <span v-html="$t('logo.title')"></span>
                    <hr class="mt-0 mb-1"/>
                    <p>{{ $t('logo.sub-title') }}</p>
                </h1>
            </div>
            <div class="col-md-8 col-sm-12 col-xs-12 text-center text-md-end">
              <ul class="list-inline">
                <li class="list-inline-item text-uppercase" v-for="(link, idx) in social" :key="idx">
                    <a :href="link.url" target="_blank"><i :class="link.icon"></i></a>
                </li>
              </ul>
                <ul class="list-inline nav-list locale-selector">
                    <li class="list-inline-item text-uppercase" v-for="(locale, idx) in locales" :key="idx">
                        <template v-if="$i18n.locale === locale">
                            <strong :class="{activeLang: $i18n.locale === locale}">{{ $t('locale.' + locale) }}</strong>
                        </template>
                        <template v-else>
                            <a href="#" @click.prevent="setLocale(locale)">
                                <strong>{{ $t('locale.' + locale) }}</strong>
                            </a>
                        </template>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>


<script>
export default {

    data: () => {
        return {
            social : [
              {
                 icon : 'social-icon my-icon-telegram',
                 url : 'https://t.me/cljamal',
              },
              {
                icon : 'social-icon my-icon-github',
                url  : 'https://github.com/cljamal',
              },
              {
                icon : 'social-icon my-icon-facebook',
                url  : 'https://www.facebook.com/JamaRolex/',
              },
              {
                icon : 'social-icon my-icon-instagram',
                url  : 'https://www.instagram.com/jamarolexoff/',
              },
            ],

            locales: [
                'ru',
                'en',
                'uz',
            ],
        }
    },

    methods: {
        getDefaultLocale() {
            let defaultLocale = window.navigator.language;
            if (!defaultLocale)
                defaultLocale = 'en';

            return defaultLocale;
        },
        getLocale() {
            let locale = localStorage.getItem('locale');
            if (!locale)
                locale = this.getDefaultLocale();

            return locale;
        },
        setLocale(locale) {
            localStorage.setItem('locale', locale);
            this.$i18n.locale = locale;
        },
    },

    mounted() {
        this.setLocale(this.getLocale());
    }

}
</script>
<style lang="scss">
  #top-nav {
      .locale-selector {
          .activeLang {
              color: #a9a9a9;
          }

          a {
              &:hover {
                  color: #908f8f;
              }
          }
      }

      .logoTitle {
          p {
              font-size: 1.17rem;
              color: #b1b1b1;
          }
      }

      .nav-list{
          li{
              &:after{
                  content: "/";
                  color: #e8e8e8;
                  display: inline-block;
                  margin-left: 10px;
              }
              &:last-child:after{
                  content: "";
                  display: none;
              }
          }
      }
      a {
          color: #555;
          text-decoration: none;
      }
  }
</style>